import React from 'react';
import ReactDOM from 'react-dom';

import App from './App';
import { HashRouter as Router } from 'react-router-dom';

export default function main({portletNamespace, contextPath,
                                 portletElementId}) {
    ReactDOM.render((
        <Router>
            <App/>
        </Router>
    ), document.getElementById(portletElementId));
}